// ============================================================================
// UI Audio System (standalone for menu/lobby sounds)
// ============================================================================
class UIAudio {
  private context: AudioContext | null = null;
  private sounds = new Map<string, AudioBuffer>();
  private initialized = false;
  private loading = false;
  private enabled = true;
  private volume = 0.7; // Default UI volume

  async init(): Promise<void> {
    if (this.initialized || this.loading) return;
    this.loading = true;
    try {
      this.context = new AudioContext();
      // Load UI sounds
      await Promise.all([
        this.loadSound('hover', './assets/audio/ui/hover.mp3'),
        this.loadSound('click', './assets/audio/ui/click.mp3'),
      ]);
      this.initialized = true;
      console.log('[UIAudio] Initialized with', this.sounds.size, 'sounds');
    } catch (e) {
      console.warn('[UIAudio] Failed to initialize:', e);
    }
    this.loading = false;
  }

  private async loadSound(key: string, url: string): Promise<void> {
    if (!this.context) return;
    try {
      const response = await fetch(url);
      const arrayBuffer = await response.arrayBuffer();
      const audioBuffer = await this.context.decodeAudioData(arrayBuffer);
      this.sounds.set(key, audioBuffer);
      console.log(`[UIAudio] Loaded: ${key}`);
    } catch (e) {
      console.warn(`[UIAudio] Failed to load ${key}:`, e);
    }
  }

  play(key: string, volume = 0.5): void {
    if (!this.enabled || !this.context || !this.sounds.has(key)) {
      // Try to init if not ready
      if (!this.initialized && !this.loading) {
        this.init();
      }
      return;
    }
    // Resume context if suspended (browser autoplay policy)
    if (this.context.state === 'suspended') {
      this.context.resume();
    }
    const buffer = this.sounds.get(key)!;
    const source = this.context.createBufferSource();
    source.buffer = buffer;
    const gainNode = this.context.createGain();
    gainNode.gain.value = volume * this.volume;
    source.connect(gainNode);
    gainNode.connect(this.context.destination);
    source.start(0);
  }

  playHover(): void {
    this.play('hover', 0.3);
  }

  playClick(): void {
    this.play('click', 0.5);
  }

  setEnabled(enabled: boolean): void {
    this.enabled = enabled;
  }
  
  setVolume(volume: number): void {
    this.volume = Math.max(0, Math.min(1, volume));
  }
}

const uiAudio = new UIAudio();

// Initialize UI audio on first user interaction (for browser autoplay policy)
const initUIAudio = () => {
  uiAudio.init();
  document.removeEventListener('click', initUIAudio);
  document.removeEventListener('keydown', initUIAudio);
  document.removeEventListener('mousemove', initUIAudio);
};
document.addEventListener('click', initUIAudio);
document.addEventListener('keydown', initUIAudio);
document.addEventListener('mousemove', initUIAudio); // Init on mouse move so sounds are ready for hover

