// Map info for hints
// Populate map dropdown based on game mode
function populateMapDropdown(gameMode: string): void {
  const currentMapId = mapSelect.value;
  mapSelect.innerHTML = '';
  
  // Filter maps based on game mode
  const filteredMaps = MAP_REGISTRY.filter(map => {
    if (gameMode === 'spike') {
      return map.hasSpikeSites; // Only show maps with spike sites
    } else {
      return !map.hasSpikeSites; // Only show deathmatch maps
    }
  });
  
  // Add options
  for (const map of filteredMaps) {
    const option = document.createElement('option');
    option.value = map.id;
    option.textContent = `${map.name} - ${map.description}`;
    mapSelect.appendChild(option);
  }
  
  // Try to keep current selection if valid, otherwise select first
  const validIds = filteredMaps.map(m => m.id);
  if (validIds.includes(currentMapId)) {
    mapSelect.value = currentMapId;
  } else if (filteredMaps.length > 0) {
    mapSelect.value = filteredMaps[0]!.id;
  }
  
  updateMapHint();
}

// Update map hint based on selection
function updateMapHint(): void {
  const mapId = mapSelect.value;
  const mapInfo = MAP_REGISTRY.find(m => m.id === mapId);
  if (mapInfo) {
    const gameMode = gameModeSelect.value;
    if (gameMode === 'spike' && !mapInfo.hasSpikeSites) {
      mapHint.textContent = ' This map has no spike sites!';
      mapHint.style.color = '#ff6b6b';
    } else if (gameMode === 'deathmatch' && mapInfo.hasSpikeSites) {
      mapHint.textContent = ' Spike sites available but unused in Deathmatch';
      mapHint.style.color = '#4ecdc4';
    } else {
      mapHint.textContent = ` ${mapInfo.description}`;
      mapHint.style.color = '#2ecc71';
    }
  } else {
    mapHint.textContent = '';
  }
}

// Update ability name displays based on hero and slot selections
function updateAbilityNames(): void {
  const hero = heroSelect.value;
  const abilities = HERO_ABILITIES[hero] || { ability1: 'Ability 1', ability2: 'Ability 2' };
  
  const qSlot = parseInt(abilityQSelect.value);
  const fSlot = parseInt(abilityFSelect.value);
  
  abilityQName.textContent = qSlot === 1 ? abilities.ability1 : abilities.ability2;
  abilityFName.textContent = fSlot === 1 ? abilities.ability1 : abilities.ability2;
}

