// Country code to flag emoji
function countryFlag(code: string): string {
  if (!code || code === 'XX') return '';
  return code.toUpperCase().replace(/./g, c => 
    String.fromCodePoint(0x1F1E6 - 65 + c.charCodeAt(0))
  );
}

// Format large numbers
function formatStatNumber(num: number): string {
  if (num >= 1000000) return (num / 1000000).toFixed(1) + 'M';
  if (num >= 1000) return (num / 1000).toFixed(1) + 'K';
  return num.toString();
}

// Load stats modal data
async function loadStatsModal(): Promise<void> {
  // Initialize PlayerStatsService if not already
  await PlayerStatsService.initialize();
  
  // Get DOM elements
  const totalPlayersEl = document.getElementById('stats-total-players');
  const totalGamesEl = document.getElementById('stats-total-games');
  const totalHoursEl = document.getElementById('stats-total-hours');
  const myStatsEl = document.getElementById('stats-my-stats');
  const countriesEl = document.getElementById('stats-countries');
  const leaderboardEl = document.getElementById('stats-leaderboard');
  
  // Load global stats
  try {
    const globalStats = await PlayerStatsService.getGlobalStats();
    if (globalStats) {
      if (totalPlayersEl) totalPlayersEl.textContent = formatStatNumber(globalStats.totalUniquePlayers || 0);
      if (totalGamesEl) totalGamesEl.textContent = formatStatNumber(globalStats.totalGamesPlayed || 0);
      if (totalHoursEl) totalHoursEl.textContent = formatStatNumber(Math.floor(globalStats.totalPlayTimeHours || 0));
      
      // Countries
      if (countriesEl && globalStats.playersByCountry) {
        const sorted = Object.entries(globalStats.playersByCountry)
          .sort((a, b) => (b[1] as number) - (a[1] as number))
          .slice(0, 8);
        
        if (sorted.length === 0) {
          countriesEl.innerHTML = '<span class="stats-loading">No country data yet</span>';
        } else {
          countriesEl.innerHTML = sorted
            .map(([code, count]) => `
              <span class="country-tag">
                ${countryFlag(code)} ${code}
                <span class="count">${count}</span>
              </span>
            `)
            .join('');
        }
      }
    }
  } catch (e) {
    console.warn('[Stats] Failed to load global stats:', e);
  }
  
  // Load my stats
  try {
    const myStats = await PlayerStatsService.getMyStats();
    if (myStatsEl) {
      if (myStats) {
        const winRate = myStats.gamesPlayed > 0 
          ? Math.round((myStats.gamesWon / myStats.gamesPlayed) * 100) 
          : 0;
        const kd = myStats.totalDeaths > 0 
          ? (myStats.totalKills / myStats.totalDeaths).toFixed(2) 
          : myStats.totalKills.toString();
        
        myStatsEl.innerHTML = `
          <div class="my-stat">
            <span class="my-stat-label">Games Played</span>
            <span class="my-stat-value">${myStats.gamesPlayed}</span>
          </div>
          <div class="my-stat">
            <span class="my-stat-label">Wins</span>
            <span class="my-stat-value">${myStats.gamesWon} (${winRate}%)</span>
          </div>
          <div class="my-stat">
            <span class="my-stat-label">Total Kills</span>
            <span class="my-stat-value">${myStats.totalKills}</span>
          </div>
          <div class="my-stat">
            <span class="my-stat-label">K/D Ratio</span>
            <span class="my-stat-value">${kd}</span>
          </div>
          <div class="my-stat">
            <span class="my-stat-label">Play Time</span>
            <span class="my-stat-value">${Math.floor(myStats.totalPlayTimeSeconds / 60)} min</span>
          </div>
          <div class="my-stat">
            <span class="my-stat-label">Country</span>
            <span class="my-stat-value">${countryFlag(myStats.country)} ${myStats.countryName || 'Unknown'}</span>
          </div>
        `;
      } else {
        myStatsEl.innerHTML = '<div class="stats-loading">Play a game to see your stats!</div>';
      }
    }
  } catch (e) {
    console.warn('[Stats] Failed to load my stats:', e);
    if (myStatsEl) myStatsEl.innerHTML = '<div class="stats-loading">Play a game to see your stats!</div>';
  }
  
  // Load leaderboard
  try {
    const topPlayers = await loadLeaderboardData();
    if (leaderboardEl) {
      if (topPlayers.length === 0) {
        leaderboardEl.innerHTML = '<li class="stats-loading">No players yet</li>';
      } else {
        leaderboardEl.innerHTML = topPlayers
          .map((player, index) => {
            const winRate = player.gamesPlayed > 0 
              ? Math.round((player.gamesWon / player.gamesPlayed) * 100) 
              : 0;
            return `
              <li>
                <span class="rank">#${index + 1}</span>
                <span class="name">${player.callsign || 'Unknown'}</span>
                <span class="wins">${player.gamesWon || 0} wins (${winRate}%)</span>
              </li>
            `;
          })
          .join('');
      }
    }
  } catch (e) {
    console.warn('[Stats] Failed to load leaderboard:', e);
    if (leaderboardEl) leaderboardEl.innerHTML = '<li class="stats-loading">Failed to load</li>';
  }
}

// Load leaderboard data from Firestore
async function loadLeaderboardData(): Promise<any[]> {
  try {
    const { getFirestore, collection, query, orderBy, limit, getDocs } = await import('firebase/firestore');
    const { initializeApp, getApps, getApp } = await import('firebase/app');
    
    const app = getApps().length === 0 ? initializeApp(FIREBASE_CONFIG) : getApp();
    const db = getFirestore(app);
    
    const q = query(
      collection(db, 'playerStats'),
      orderBy('gamesWon', 'desc'),
      limit(5)
    );
    
    const snapshot = await getDocs(q);
    return snapshot.docs.map(doc => doc.data());
  } catch (e) {
    console.warn('[Stats] Failed to query leaderboard:', e);
    return [];
  }
}

// -------------------------------------------------------------------------
// Lobby Browser Functions
// -------------------------------------------------------------------------

let lobbyBrowserUnsubscribe: (() => void) | null = null;

async function openLobbyBrowser(): Promise<void> {
  console.log('[LobbyBrowser] Opening lobby browser...');
  
  // Validate player name first
  const playerName = playerNameInput.value.trim();
  if (!playerName) {
    console.log('[LobbyBrowser] No player name entered');
    playerNameInput.focus();
    playerNameInput.style.borderColor = '#ff4444';
    setTimeout(() => { playerNameInput.style.borderColor = ''; }, 2000);
    return;
  }
  
  lobbyBrowserModal.classList.remove('hidden');
  connectionUI.classList.add('hidden');
  
  try {
    // Unsubscribe from any existing subscription first
    if (lobbyBrowserUnsubscribe) {
      lobbyBrowserUnsubscribe();
      lobbyBrowserUnsubscribe = null;
    }
    
    // Initialize service and fetch lobbies
    console.log('[LobbyBrowser] Initializing service...');
    await LobbyBrowserService.initialize();
    
    // Clean up stale lobbies first
    console.log('[LobbyBrowser] Cleaning stale lobbies...');
    await LobbyBrowserService.cleanupStaleLobbies();
    
    // Subscribe to real-time updates (this will immediately fire with current data)
    console.log('[LobbyBrowser] Subscribing to lobbies...');
    lobbyBrowserUnsubscribe = LobbyBrowserService.subscribeToLobbies((lobbies) => {
      console.log('[LobbyBrowser] Real-time update:', lobbies.length, 'lobbies');
      renderLobbies(lobbies);
    });
    
    console.log('[LobbyBrowser] Lobby browser ready');
  } catch (e) {
    console.error('[LobbyBrowser] Error opening lobby browser:', e);
  }
}

function closeLobbyBrowser(): void {
  lobbyBrowserModal.classList.add('hidden');
  connectionUI.classList.remove('hidden');
  
  // Unsubscribe from updates
  if (lobbyBrowserUnsubscribe) {
    lobbyBrowserUnsubscribe();
    lobbyBrowserUnsubscribe = null;
  }
}

async function refreshLobbies(): Promise<void> {
  // Add spinning animation to refresh button
  refreshLobbiesButton.classList.add('spinning');
  
  try {
    const lobbies = await LobbyBrowserService.getPublicLobbies();
    renderLobbies(lobbies);
  } catch (e) {
    console.error('[LobbyBrowser] Failed to refresh lobbies:', e);
  }
  
  // Remove spinning animation after short delay
  setTimeout(() => {
    refreshLobbiesButton.classList.remove('spinning');
  }, 500);
}

function renderLobbies(lobbies: PublicLobby[]): void {
  // Clear existing lobby items (but not the empty state)
  const existingItems = lobbyList.querySelectorAll('.lobby-item');
  existingItems.forEach(item => item.remove());
  
  if (lobbies.length === 0) {
    lobbyEmpty.style.display = 'block';
    return;
  }
  
  lobbyEmpty.style.display = 'none';
  
  for (const lobby of lobbies) {
    const item = document.createElement('div');
    item.className = 'lobby-item';
    
    const modeLabel = lobby.gameMode === 'spike' ? ' Spike' : ' Deathmatch';
    const mapInfo = MAP_REGISTRY.find(m => m.id === lobby.mapId);
    const mapName = mapInfo?.name || lobby.mapId;
    
    item.innerHTML = `
      <div class="lobby-info">
        <div class="lobby-host">${escapeHtml(lobby.hostName)}</div>
        <div class="lobby-details">
          <span>${modeLabel}</span>
          <span> ${mapName}</span>
        </div>
      </div>
      <span class="lobby-players">${lobby.currentPlayers}/${lobby.maxPlayers}</span>
      <button class="join-btn" data-room-code="${lobby.roomCode}">Join</button>
    `;
    
    // Add click handler for join button
    const joinBtn = item.querySelector('.join-btn') as HTMLButtonElement;
    joinBtn.addEventListener('click', () => {
      joinPublicLobby(lobby.roomCode);
    });
    
    lobbyList.insertBefore(item, lobbyEmpty);
  }
}

function escapeHtml(text: string): string {
  const div = document.createElement('div');
  div.textContent = text;
  return div.innerHTML;
}

async function joinPublicLobby(roomCode: string): Promise<void> {
  // Close the lobby browser
  closeLobbyBrowser();
  
  // Set the room code and trigger the join flow
  roomCodeInput.value = roomCode;
  
  // Trigger the same flow as manual join
  await handleConnect();
}

// Load saved settings
function loadSettings(): void {
  const saved = localStorage.getItem('cybergrid_settings');
  if (saved) {
    try {
      const parsed = JSON.parse(saved);
      gameSettings = { ...gameSettings, ...parsed };
      
      // Validate ability slot settings - must be 1 or 2, and different from each other
      if (gameSettings.abilityQSlot !== 1 && gameSettings.abilityQSlot !== 2) {
        gameSettings.abilityQSlot = 1;
      }
      if (gameSettings.abilityFSlot !== 1 && gameSettings.abilityFSlot !== 2) {
        gameSettings.abilityFSlot = 2;
      }
      // If both are the same, reset to defaults
      if (gameSettings.abilityQSlot === gameSettings.abilityFSlot) {
        gameSettings.abilityQSlot = 1;
        gameSettings.abilityFSlot = 2;
      }
      
      console.log('[Settings] Loaded:', gameSettings);
    } catch (e) {
      console.warn('Failed to load settings:', e);
    }
  }
  
  // Apply to UI
  botCountSlider.value = String(gameSettings.enemyBotCount);
  botCountValue.textContent = String(gameSettings.enemyBotCount);
  friendlyBotsSlider.value = String(gameSettings.friendlyBotCount);
  friendlyBotsValue.textContent = String(gameSettings.friendlyBotCount);
  botDifficultySelect.value = gameSettings.botDifficulty;
  aiModeSelect.value = gameSettings.aiMode || 'rule_based';
  gameModeSelect.value = gameSettings.gameMode;
  
  // Populate maps based on game mode, then set saved map
  populateMapDropdown(gameSettings.gameMode);
  if (gameSettings.mapId) {
    mapSelect.value = gameSettings.mapId;
  }
  
  heroSelect.value = gameSettings.playerHero;
  abilityQSelect.value = String(gameSettings.abilityQSlot);
  abilityFSelect.value = String(gameSettings.abilityFSlot);
  updateAbilityNames();
  updateMapHint();
}

function saveSettings(): void {
  gameSettings = {
    enemyBotCount: parseInt(botCountSlider.value),
    friendlyBotCount: parseInt(friendlyBotsSlider.value),
    botDifficulty: botDifficultySelect.value as 'easy' | 'medium' | 'hard',
    gameMode: gameModeSelect.value as 'deathmatch' | 'spike',
    mapId: mapSelect.value,
    playerHero: heroSelect.value,
    abilityQSlot: parseInt(abilityQSelect.value) as 1 | 2,
    abilityFSlot: parseInt(abilityFSelect.value) as 1 | 2,
    totalRounds: gameSettings.totalRounds,  // Preserve from lobby setting
    aiMode: aiModeSelect.value as 'rule_based' | 'slm',
  };
  localStorage.setItem('cybergrid_settings', JSON.stringify(gameSettings));
  settingsPanel.classList.add('hidden');
}

// Gameplay settings (saved separately)
interface GameplaySettings {
  volume: number;
  sensitivity: number;
  showFps: boolean;
}

// Audio settings interface
interface AudioSettingsStorage {
  masterVolume: number;
  masterEnabled: boolean;
  musicVolume: number;
  musicEnabled: boolean;
  sfxVolume: number;
  sfxEnabled: boolean;
  voiceVolume: number;
  voiceEnabled: boolean;
  uiVolume: number;
  uiEnabled: boolean;
}

let gameplaySettings: GameplaySettings = {
  volume: 100,
  sensitivity: 1.0,
  showFps: true,
};

let audioSettings: AudioSettingsStorage = {
  masterVolume: 80,
  masterEnabled: true,
  musicVolume: 50,
  musicEnabled: true,
  sfxVolume: 80,
  sfxEnabled: true,
  voiceVolume: 100,
  voiceEnabled: true,
  uiVolume: 70,
  uiEnabled: true,
};

function loadAudioSettings(): void {
  const saved = localStorage.getItem('cybergrid_audio');
  if (saved) {
    try {
      audioSettings = { ...audioSettings, ...JSON.parse(saved) };
    } catch (e) {
      console.warn('Failed to load audio settings:', e);
    }
  }
  // Apply to UI (both panels)
  updateAudioSettingsUI();
  // Apply to uiAudio for menu sounds
  const masterVol = audioSettings.masterVolume / 100;
  const uiVol = audioSettings.uiVolume / 100;
  uiAudio.setVolume(masterVol * uiVol);
  uiAudio.setEnabled(audioSettings.masterEnabled && audioSettings.uiEnabled);
}

function saveAudioSettings(): void {
  localStorage.setItem('cybergrid_audio', JSON.stringify(audioSettings));
}

