// -------------------------------------------------------------------------
// Buy Menu Functions
// -------------------------------------------------------------------------

const WEAPON_PRICES: Record<string, number> = {
  'burst_pistol': 0,
  'neo_smg9': 1100,
  'pulse_rifle': 1500,
  'scatter_shot': 1400,
  'rail_sniper': 2100,
  'shard_launcher': 600,
  'nano_repeater': 900,
  'arc_welder': 1300,
  'phase_repeater': 1800,
  'plasma_cannon': 2800,
};

// Help Panel Functions
function showHelpPanel(autoHide: boolean = false): void {
  helpPanelVisible = true;
  helpPanel.classList.add('visible');
  
  // Clear any existing auto-hide timer
  if (helpAutoHideTimer !== null) {
    clearTimeout(helpAutoHideTimer);
    helpAutoHideTimer = null;
  }
  
  // Auto-hide after 5 seconds if requested
  if (autoHide) {
    helpAutoHideTimer = window.setTimeout(() => {
      hideHelpPanel();
    }, 5000);
  }
}

function hideHelpPanel(): void {
  if (!helpPanelVisible) return; // Already hidden
  helpPanelVisible = false;
  helpPanel.classList.remove('visible');
  if (helpAutoHideTimer !== null) {
    clearTimeout(helpAutoHideTimer);
    helpAutoHideTimer = null;
  }
}

function openBuyMenu(): void {
  if (!game) return;
  const player = game.getWorld().getLocalPlayer();
  if (!player || !player.alive) return;
  
  updateBuyMenuUI();
  buyMenu.classList.remove('hidden');
  
  // Start auto-close timer (3 seconds)
  startBuyMenuAutoCloseTimer();
}

function closeBuyMenu(): void {
  buyMenu.classList.add('hidden');
  
  // Clear auto-close timer
  if (buyMenuAutoCloseTimer !== null) {
    clearTimeout(buyMenuAutoCloseTimer);
    buyMenuAutoCloseTimer = null;
  }
}

function startBuyMenuAutoCloseTimer(): void {
  // Clear any existing timer
  if (buyMenuAutoCloseTimer !== null) {
    clearTimeout(buyMenuAutoCloseTimer);
  }
  
  // Set new timer for 3 seconds
  buyMenuAutoCloseTimer = window.setTimeout(() => {
    closeBuyMenu();
  }, 3000);
}

function updateBuyMenuUI(): void {
  if (!game) return;
  const player = game.getWorld().getLocalPlayer();
  if (!player) return;
  
  // Update credits display
  buyCredits.textContent = String(player.credits);
  
  // Update weapon item states
  weaponItems.forEach(item => {
    const weaponId = item.dataset.weapon || '';
    const slotStr = item.dataset.slot || '1';
    const slot = parseInt(slotStr) - 1;
    const price = WEAPON_PRICES[weaponId] || 0;
    
    // Check if player owns this weapon
    const owned = player.weaponInventory[slot] !== null;
    const canAfford = player.credits >= price;
    
    // Reset classes
    item.classList.remove('owned', 'cant-afford');
    
    if (owned) {
      item.classList.add('owned');
      const priceEl = item.querySelector('.weapon-price');
      if (priceEl) priceEl.textContent = 'OWNED';
    } else if (!canAfford && price > 0) {
      item.classList.add('cant-afford');
    }
  });
}

function tryBuyWeapon(weaponId: string): void {
  if (!game) return;
  const player = game.getWorld().getLocalPlayer();
  if (!player || !player.alive) return;
  
  // Map string to WeaponId enum
  const weaponIdMap: Record<string, import('@/shared/types').WeaponId> = {
    'burst_pistol': 'burst_pistol' as import('@/shared/types').WeaponId,
    'neo_smg9': 'neo_smg9' as import('@/shared/types').WeaponId,
    'pulse_rifle': 'pulse_rifle' as import('@/shared/types').WeaponId,
    'scatter_shot': 'scatter_shot' as import('@/shared/types').WeaponId,
    'rail_sniper': 'rail_sniper' as import('@/shared/types').WeaponId,
    'shard_launcher': 'shard_launcher' as import('@/shared/types').WeaponId,
    'nano_repeater': 'nano_repeater' as import('@/shared/types').WeaponId,
    'arc_welder': 'arc_welder' as import('@/shared/types').WeaponId,
    'phase_repeater': 'phase_repeater' as import('@/shared/types').WeaponId,
    'plasma_cannon': 'plasma_cannon' as import('@/shared/types').WeaponId,
  };
  
  const enumWeaponId = weaponIdMap[weaponId];
  if (!enumWeaponId) return;
  
  // Check if already owned
  const slot = player.getWeaponSlotForId(enumWeaponId) - 1;
  if (player.weaponInventory[slot] !== null) {
    // Already owned - just switch to it
    player.switchToWeaponSlot(slot + 1);
    closeBuyMenu();
    return;
  }
  
  // Try to buy
  const success = player.buyWeapon(enumWeaponId);
  if (success) {
    updateBuyMenuUI();
    // Play purchase sound or effect here
  }
  
  // Reset auto-close timer on any weapon interaction (not just successful purchase)
  startBuyMenuAutoCloseTimer();
}

function pauseGame(): void {
  if (!game) return;
  game.pause();
  pauseMenu.classList.remove('hidden');
}

function resumeGame(): void {
  if (!game) return;
  pauseMenu.classList.add('hidden');
  saveGameplaySettings();
  game.resume();
}

function quitToMenu(): void {
  if (!game) return;
  
  // End stats session before stopping game
  game.endStatsSession();
  
  // Cleanup voice chat
  cleanupVoiceChat();
  
  game.stop();
  game = null;
  gameStarted = false; // Reset so Start Game button works again
  stopRoundEndPolling();
  stopLobbyCountdown();
  
  // Hide all game UI
  canvas.classList.add('hidden');
  pauseMenu.classList.add('hidden');
  roundOverPanel.classList.add('hidden');
  roomCodeDisplay.classList.add('hidden');
  lobbyScreen.classList.add('hidden');
  helpPanel.classList.remove('visible');
  buyMenu.classList.add('hidden');
  
  // Show main menu UI
  connectionUI.classList.remove('hidden');
  settingsButton.classList.remove('hidden');
  
  // Reset join UI
  hostButton.classList.remove('hidden');
  joinButton.classList.remove('hidden');
  roomCodeInput.classList.add('hidden');
  joinButtons.classList.add('hidden');
  roomCodeInput.value = '';
  
  // Reset lobby state
  lobbyState.isHost = false;
  lobbyState.players = [];
  lobbyState.roomCode = '';
  lobbyState.isPublic = false;
  gameStarted = false;
  
  // Disconnect network if still connected
  if (lobbyNetwork) {
    lobbyNetwork.disconnect();
    lobbyNetwork = null;
  }
}

// -------------------------------------------------------------------------
// Lobby System
// -------------------------------------------------------------------------

interface LobbyPlayer {
  id: string;
  name: string;
  isHost: boolean;
}

interface LobbyState {
  isHost: boolean;
  roomCode: string;
  players: LobbyPlayer[];
  maxPlayers: number;
  countdownActive: boolean;
  countdownValue: number;
  isPublic: boolean;
}

const lobbyState: LobbyState = {
  isHost: false,
  roomCode: '',
  players: [],
  maxPlayers: 2,
  countdownActive: false,
  countdownValue: 5,
  isPublic: false,
};

let lobbyCountdownInterval: number | null = null;

function generateRoomCode(): string {
  const letters = 'ABCDEFGHJKLMNPQRSTUVWXYZ';
  const digits = '0123456789';
  
  let code = '';
  for (let i = 0; i < 4; i++) {
    code += letters.charAt(Math.floor(Math.random() * letters.length));
  }
  for (let i = 0; i < 2; i++) {
    code += digits.charAt(Math.floor(Math.random() * digits.length));
  }
  
  return code;
}

function showLobby(isHost: boolean, roomCode: string, playerName: string): void {
  // Reset game started flag so Start Game button works
  gameStarted = false;
  
  lobbyState.isHost = isHost;
  lobbyState.roomCode = roomCode;
  lobbyState.maxPlayers = parseInt(lobbyPlayerCountSelect.value);
  lobbyState.countdownActive = false;
  lobbyState.isPublic = isHost ? lobbyMakePublicCheckbox.checked : false;
  
  // Add self as first player
  lobbyState.players = [{
    id: 'local',
    name: playerName,
    isHost: isHost,
  }];
  
  // Update UI
  lobbyRoomCode.textContent = roomCode;
  lobbyMaxPlayers.textContent = String(lobbyState.maxPlayers);
  
  // Show/hide host controls - ONLY host sees these
  hostSettings.style.display = isHost ? 'block' : 'none';
  startGameButton.style.display = isHost ? 'block' : 'none';
  
  // Update waiting text based on role
  if (isHost) {
    lobbyWaitingText.innerHTML = 'Waiting for players<span class="dots">...</span>';
  } else {
    lobbyWaitingText.innerHTML = 'Connecting to host<span class="dots">...</span>';
  }
  
  // Hide connection UI, show lobby
  connectionUI.classList.add('hidden');
  settingsPanel.classList.add('hidden');
  settingsButton.classList.add('hidden');
  lobbyScreen.classList.remove('hidden');
  
  // If host, sync the Make Public checkbox state with lobbyState
  // Don't auto-create here - let the checkbox change handler do it
  // This prevents duplicate lobby creation
  if (isHost) {
    lobbyMakePublicCheckbox.checked = lobbyState.isPublic;
    console.log('[Lobby] isHost:', isHost, 'isPublic:', lobbyState.isPublic);
  }
  
  updateLobbyUI();
}

function updateLobbyUI(): void {
  // Update player count
  lobbyCurrentPlayers.textContent = String(lobbyState.players.length);
  lobbyMaxPlayers.textContent = String(lobbyState.maxPlayers);
  
  // Update player list
  lobbyPlayerList.innerHTML = '';
  for (const player of lobbyState.players) {
    const item = document.createElement('div');
    item.className = `player-item${player.isHost ? ' host' : ''}`;
    item.innerHTML = `
      <span class="name">${player.name}</span>
      ${player.isHost ? '<span class="tag">Host</span>' : ''}
    `;
    lobbyPlayerList.appendChild(item);
  }
  
  // Update start button state (host only)
  if (lobbyState.isHost) {
    const canStart = lobbyState.players.length >= lobbyState.maxPlayers || lobbyState.maxPlayers === 1;
    startGameButton.disabled = !canStart;
    
    if (canStart && !lobbyState.countdownActive) {
      lobbyWaitingText.textContent = 'All players ready!';
    } else if (!lobbyState.countdownActive) {
      lobbyWaitingText.innerHTML = 'Waiting for players<span class="dots">...</span>';
    }
    
    // Update public lobby player count
    if (lobbyState.isPublic) {
      LobbyBrowserService.updateLobbyPlayerCount(lobbyState.roomCode, lobbyState.players.length);
    }
  } else {
    // Joiner - show different message based on connection state
    const hasHost = lobbyState.players.some(p => p.isHost && p.id !== 'local');
    if (hasHost) {
      lobbyWaitingText.innerHTML = 'Waiting for host to start<span class="dots">...</span>';
    } else {
      lobbyWaitingText.innerHTML = 'Connecting to host<span class="dots">...</span>';
    }
  }
  
  // Auto-start countdown when full
  if (lobbyState.players.length >= lobbyState.maxPlayers && !lobbyState.countdownActive && lobbyState.maxPlayers > 1) {
    startLobbyCountdown();
  }
}

function startLobbyCountdown(): void {
  if (lobbyState.countdownActive) return;
  
  lobbyState.countdownActive = true;
  lobbyState.countdownValue = 5;
  
  lobbyWaitingText.classList.add('hidden');
  lobbyCountdown.classList.remove('hidden');
  lobbyCountdown.textContent = String(lobbyState.countdownValue);
  
  lobbyCountdownInterval = window.setInterval(() => {
    lobbyState.countdownValue--;
    lobbyCountdown.textContent = String(lobbyState.countdownValue);
    
    if (lobbyState.countdownValue <= 0) {
      stopLobbyCountdown();
      startGameFromLobby();
    }
  }, 1000);
}

function stopLobbyCountdown(): void {
  if (lobbyCountdownInterval !== null) {
    clearInterval(lobbyCountdownInterval);
    lobbyCountdownInterval = null;
  }
  lobbyState.countdownActive = false;
  lobbyCountdown.classList.add('hidden');
  lobbyWaitingText.classList.remove('hidden');
}

let gameStarted = false; // Prevent double start

function leaveLobby(): void {
  stopLobbyCountdown();
  lobbyScreen.classList.add('hidden');
  connectionUI.classList.remove('hidden');
  settingsButton.classList.remove('hidden');
  
  // Remove from public lobby listings if host
  if (lobbyState.isHost && lobbyState.isPublic) {
    LobbyBrowserService.removeLobby(lobbyState.roomCode);
  }
  
  // Reset join UI
  hostButton.classList.remove('hidden');
  joinButton.classList.remove('hidden');
  roomCodeInput.classList.add('hidden');
  joinButtons.classList.add('hidden');
  roomCodeInput.value = '';
  
  // Reset lobby state
  lobbyState.isHost = false;
  lobbyState.players = [];
  lobbyState.roomCode = '';
  lobbyState.isPublic = false;
  gameStarted = false; // Reset game started flag
  
  // Disconnect lobby network if exists
  if (lobbyNetwork) {
    lobbyNetwork.disconnect();
    lobbyNetwork = null;
  }
}

async function startGameFromLobby(): Promise<void> {
  // Prevent double execution
  if (gameStarted) {
    console.log(`[Main] startGameFromLobby already called, ignoring`);
    return;
  }
  gameStarted = true;
  
  const playerName = playerNameInput.value.trim() || 'Player';
  
  // Hide lobby
  lobbyScreen.classList.add('hidden');
  
  // Remove from public lobby listings (game is starting)
  if (lobbyState.isHost && lobbyState.isPublic) {
    LobbyBrowserService.removeLobby(lobbyState.roomCode);
  }
  
  // Update settings with lobby values
  gameSettings.totalRounds = parseInt(lobbyRoundCountSelect.value);
  
  // Get the network to pass to the game (don't disconnect it!)
  const networkToPass = lobbyNetwork;
  lobbyNetwork = null;  // Transfer ownership to game
  
  // Remove lobby event listeners - Game will set up its own
  if (networkToPass) {
    networkToPass.removeAllListeners();
    console.log(`[Main] Removed lobby listeners from network`);
  }
  
  // If host, broadcast game start to all peers BEFORE transferring network
  if (lobbyState.isHost && networkToPass) {
    console.log(`[Main] Broadcasting GameStart to all peers`);
    networkToPass.broadcast({
      type: PacketType.GameStart,
      timestamp: Date.now(),
      senderId: networkToPass.getLocalPeerId(),
      data: {
        roomCode: lobbyState.roomCode,
        totalRounds: gameSettings.totalRounds,
        mapId: gameSettings.mapId,
        gameMode: gameSettings.gameMode,
      },
    });
  }
  
  // Create game with settings and existing network connection
  // Pass lobby players so game knows their names
  console.log(`[Main] lobbyState.players RAW:`, JSON.stringify(lobbyState.players));
  const lobbyPlayersForGame = lobbyState.players.map(p => ({
    id: p.id,
    name: p.name,
    isHost: p.isHost,
  }));
  
  console.log(`[Main] Passing ${lobbyPlayersForGame.length} lobby players to game:`, JSON.stringify(lobbyPlayersForGame));
  console.log(`[Main] networkToPass exists: ${!!networkToPass}, peers: ${networkToPass?.getPeerIds().length || 0}`);
  
  if (networkToPass) {
    console.log(`[Main] Creating Game with EXISTING network (${networkToPass.getPeerIds().length} peers)`);
    game = new Game({
      canvas,
      playerName,
      settings: gameSettings,
      network: networkToPass,  // Pass existing lobby network
      lobbyPlayers: lobbyPlayersForGame,  // Pass lobby players with names
      appVersion: __APP_VERSION__,
    });
  } else {
    console.log(`[Main] Creating Game WITHOUT network (will create new)`);
    game = new Game({
      canvas,
      playerName,
      settings: gameSettings,
      lobbyPlayers: lobbyPlayersForGame,
      appVersion: __APP_VERSION__,
    });
  }

  await game.init();
  
  // Apply gameplay settings to game
  game.setShowFPS(gameplaySettings.showFps);
  
  // Apply audio settings to game
  game.updateAudioSettings(audioSettings);

  // Host game with the lobby's room code (game will use passed network)
  if (lobbyState.isHost) {
    const roomCode = await game.hostGame(lobbyState.roomCode);
    console.log(`[Main] Starting game as HOST: ${roomCode} (Best of ${gameSettings.totalRounds})`);
  } else {
    // Joining player - use joinGame instead
    await game.joinGame(lobbyState.roomCode);
    console.log(`[Main] Joined game: ${lobbyState.roomCode}`);
  }
  
  // Hide room code display during gameplay (only needed in lobby)
  roomCodeDisplay.classList.add('hidden');
  
  // Show game canvas
  canvas.classList.remove('hidden');

  // Start game loop
  game.start();
  
  // Start player stats tracking session
  game.startStatsSession();
  
  // Track match start in analytics
  game.trackMatchStart();
  
  // Show help panel briefly for new players
  showHelpPanel(true);
  
  // Start polling for round end
  startRoundEndPolling();
  
  // Initialize and show voice chat
  initVoiceChat();
}

// Round Over functions
let roundEndPollingId: number | null = null;
let roundOverShown = false;
let idleTimeoutId: number | null = null;
let lastInteractionTime: number = Date.now();

function showRoundOver(
  winningTeam: 'attackers' | 'defenders', 
  attackerScore: number, 
  defenderScore: number,
  isMatchOver: boolean,
  roundsToWin: number,
  isForfeit: boolean = false,
  forfeitPlayerName: string = ''
): void {
  // Get the round over panel header
  const header = roundOverPanel.querySelector('h2');
  
  // Update match info
  matchInfo.textContent = `First to ${roundsToWin} wins`;
  
  if (isMatchOver) {
    // Match is over - show match winner and Play Again button
    if (isForfeit) {
      if (header) header.textContent = ' Player Disconnected';
      winnerText.textContent = `${forfeitPlayerName} has left the match`;
      winnerText.className = 'winner-text';
      matchInfo.textContent = 'Opponent forfeited - You Win!';
    } else if (winningTeam === 'attackers') {
      if (header) header.textContent = ' Match Over!';
      winnerText.textContent = ' ATTACKERS WIN THE MATCH!';
      winnerText.className = 'winner-text attackers';
    } else {
      if (header) header.textContent = ' Match Over!';
      winnerText.textContent = ' DEFENDERS WIN THE MATCH!';
      winnerText.className = 'winner-text defenders';
    }
    
    if (!isForfeit) {
      matchInfo.textContent = 'Match Complete!';
    }
    
    // Show Play Again and Main Menu buttons, hide countdown
    playAgainButton.classList.remove('hidden');
    playAgainButton.textContent = 'Play Again';
    mainMenuButton.classList.remove('hidden');
    countdownDisplay.classList.add('hidden');
    
    // Record match result to player stats
    if (game) {
      game.recordMatchResult();
    }
    
    // Start idle timeout (60 seconds of no interaction)
    startIdleTimeout();
  } else {
    // Round is over - show round winner and countdown
    if (header) header.textContent = ' Round Over';
    if (winningTeam === 'attackers') {
      winnerText.textContent = ' Attackers Win Round!';
      winnerText.className = 'winner-text attackers';
    } else {
      winnerText.textContent = ' Defenders Win Round!';
      winnerText.className = 'winner-text defenders';
    }
    
    // Show countdown, hide Play Again and Main Menu buttons
    countdownDisplay.classList.remove('hidden');
    playAgainButton.classList.add('hidden');
    mainMenuButton.classList.add('hidden');
  }
  
  // Update scores
  scoreAttackers.textContent = String(attackerScore);
  scoreDefenders.textContent = String(defenderScore);
  
  // Show panel
  roundOverPanel.classList.remove('hidden');
  roundOverShown = true;
}

function updateCountdownDisplay(): void {
  if (!game) return;
  
  const countdownInfo = game.getCountdownInfo();
  if (!countdownInfo) return;
  
  const seconds = Math.ceil(countdownInfo.countdownTime / 1000);
  
  if (countdownInfo.isPaused) {
    countdownText.textContent = `PAUSED by ${countdownInfo.pausedBy}`;
    countdownText.classList.add('paused');
    pauseCountdownButton.textContent = ' Resume';
    pauseCountdownButton.classList.add('paused');
  } else {
    countdownText.textContent = `Next round in ${seconds}s`;
    countdownText.classList.remove('paused');
    pauseCountdownButton.textContent = ' Pause';
    pauseCountdownButton.classList.remove('paused');
  }
}

function startRoundEndPolling(): void {
  roundOverShown = false;
  
  // Poll every 100ms
  roundEndPollingId = window.setInterval(() => {
    if (!game) {
      stopRoundEndPolling();
      return;
    }
    
    const info = game.getRoundEndInfo();
    if (info && !roundOverShown) {
      showRoundOver(
        info.winningTeam, 
        info.attackerScore, 
        info.defenderScore,
        info.isMatchOver,
        info.roundsToWin,
        info.isForfeit,
        info.forfeitPlayerName
      );
    }
    
    // Update countdown display while round over panel is shown
    if (roundOverShown && info && !info.isMatchOver) {
      updateCountdownDisplay();
    }
    
    // Hide round over panel when round becomes active again
    if (roundOverShown && game.isRoundActive()) {
      roundOverPanel.classList.add('hidden');
      roundOverShown = false;
    }
  }, 100);
}

function stopRoundEndPolling(): void {
  if (roundEndPollingId !== null) {
    clearInterval(roundEndPollingId);
    roundEndPollingId = null;
  }
  
  // Clear idle timeout if active
  clearIdleTimeout();
}

function startIdleTimeout(): void {
  // Clear any existing timeout
  clearIdleTimeout();
  
  // Reset last interaction time
  lastInteractionTime = Date.now();
  
  // Start checking for idle (every second)
  idleTimeoutId = window.setInterval(() => {
    const idleTime = Date.now() - lastInteractionTime;
    
    // If idle for 60 seconds, return to menu
    if (idleTime >= 60000) {
      console.log('[Main] 60 seconds idle - returning to menu');
      clearIdleTimeout();
      quitToMenu();
    }
  }, 1000);
}

function clearIdleTimeout(): void {
  if (idleTimeoutId !== null) {
    clearInterval(idleTimeoutId);
    idleTimeoutId = null;
  }
}

function resetIdleTimer(): void {
  lastInteractionTime = Date.now();
}

function playAgain(): void {
  if (!game) return;
  
  // User interaction - reset idle timer
  resetIdleTimer();
  clearIdleTimeout();
  
  // Check if this is match over
  const info = game.getRoundEndInfo();
  
  if (info?.isMatchOver) {
    // Match is over - full restart
    roundOverPanel.classList.add('hidden');
    roundOverShown = false;
    game.restartGame();
  }
  // Note: For round over, the countdown handles starting the next round automatically
}

function toggleCountdownPause(): void {
  if (!game) return;
  game.toggleCountdownPause();
}

// ============================================================================
// Voice Chat Functions
// ============================================================================

function initVoiceChat(): void {
  const voiceChat = VoiceChatService.getInstance();
  voiceChat.initialize();
  
  // Show microphone button during gameplay
  if (micToggleButton) {
    micToggleButton.classList.add('show');
    updateMicButtonUI(false);
  }
  
  // Set up voice chat based on game configuration
  if (game) {
    const localPlayer = game.getWorld().getLocalPlayer();
    if (localPlayer) {
      // Get all players and filter by team (or all if 1v1)
      const allPlayers = game.getWorld().getPlayers();
      const is1v1 = allPlayers.length === 2;
      
      if (is1v1) {
        // In 1v1, allow communication with the other player
        const allowedPeers = allPlayers
          .filter((p) => p.id !== localPlayer.id)
          .map((p) => p.peerId);
        voiceChat.setAllowedPeers(allowedPeers);
      } else {
        // In team games, only allow communication with teammates
        const allowedPeers = allPlayers
          .filter((p) => p.team === localPlayer.team && p.id !== localPlayer.id)
          .map((p) => p.peerId);
        voiceChat.setAllowedPeers(allowedPeers);
      }
    }
  }
}

function cleanupVoiceChat(): void {
  const voiceChat = VoiceChatService.getInstance();
  voiceChat.cleanup();
  
  // Hide microphone button
  if (micToggleButton) {
    micToggleButton.classList.remove('show');
  }
}

function updateMicButtonUI(enabled: boolean): void {
  if (!micToggleButton) return;
  
  if (enabled) {
    micToggleButton.classList.add('active');
    micToggleButton.classList.remove('muted');
    micToggleButton.innerHTML = '';
    micToggleButton.title = 'Microphone ON (click to mute)';
  } else {
    micToggleButton.classList.remove('active');
    micToggleButton.classList.add('muted');
    micToggleButton.innerHTML = '';
    micToggleButton.title = 'Microphone OFF (click to enable)';
  }
}

// Initialize
