  [SFX.ScatterGunShoot]: './assets/audio/weapons/scatter_gun_shoot.mp3',
  [SFX.FusionCannonShoot]: './assets/audio/weapons/fusion_cannon_shoot.mp3',
  [SFX.WeaponReload]: './assets/audio/weapons/reload.mp3',
  [SFX.WeaponEmpty]: './assets/audio/weapons/empty_click.mp3',
  [SFX.WeaponSwitch]: './assets/audio/weapons/weapon_switch.mp3',
  
  // Player
  [SFX.FootstepMetal]: './assets/audio/player/footstep_metal.mp3',
  [SFX.PlayerDamage]: './assets/audio/player/damage.mp3',
  [SFX.PlayerDeath]: './assets/audio/player/death.mp3',
  [SFX.PlayerRespawn]: './assets/audio/player/respawn.mp3',
  
  // Combat
  [SFX.BulletImpactWall]: './assets/audio/combat/impact_wall.mp3',
  [SFX.BulletImpactPlayer]: './assets/audio/combat/impact_player.mp3',
  [SFX.Headshot]: './assets/audio/combat/headshot.mp3',
  [SFX.KillConfirmed]: './assets/audio/combat/kill_confirmed.mp3',
  
  // Abilities
  [SFX.AbilityCast]: './assets/audio/abilities/cast.mp3',
  [SFX.Explosion]: './assets/audio/abilities/explosion.mp3',
  [SFX.Teleport]: './assets/audio/abilities/teleport.mp3',
  
  // Game State
  [SFX.RoundStart]: './assets/audio/game/round_start.mp3',
  [SFX.RoundEnd]: './assets/audio/game/round_end.mp3',
  [SFX.MatchWin]: './assets/audio/game/match_win.mp3',
  [SFX.MatchLose]: './assets/audio/game/match_lose.mp3',
  [SFX.CountdownTick]: './assets/audio/game/countdown_tick.mp3',
  [SFX.CountdownGo]: './assets/audio/game/countdown_go.mp3',
  [SFX.AirplaneFlyby]: './assets/audio/game/airplane-flyby.mp3',
  [SFX.ParachuteDrop]: './assets/audio/game/parachute-drop.mp3',
  [SFX.MissileIncoming]: './assets/audio/_depo/missile-explosion-168600.mp3',
  [SFX.WeaponPickup]: './assets/audio/game/weapon-equipped.mp3',
  [SFX.HealthPickup]: './assets/audio/game/health-pickup.mp3',
  [SFX.ItemPickup]: './assets/audio/game/item-pickup.mp3',
  
  // UI
  [SFX.UIClick]: './assets/audio/ui/click.mp3',
  [SFX.UIHover]: './assets/audio/ui/hover.mp3',
  [SFX.UISuccess]: './assets/audio/ui/success.mp3',
  [SFX.UIError]: './assets/audio/ui/error.mp3',
  
  // Music
  [Music.MenuTheme]: './assets/audio/music/menu_theme.mp3',
