// ============================================================================
// GAME MAP
// ============================================================================

import { MapData, Vector2, Rectangle, Team, SpawnPoint } from '@/shared/types';
import { CollisionBody, ColliderType, CollisionLayers, CollisionWorld } from '@/engine/CollisionSystem';
import { generateId } from '@/shared/utils';

export class GameMap {
  public readonly data: MapData;
  private collisionBodies: CollisionBody[] = [];

  constructor(data: MapData) {
    this.data = data;
    this.buildCollision();
  }

  private buildCollision(): void {
    // Convert tile map to collision bodies
    for (let y = 0; y < this.data.tiles.length; y++) {
      const row = this.data.tiles[y];
      if (!row) continue;

      for (let x = 0; x < row.length; x++) {
        const tile = row[x];
        
        // 0 = empty, 1 = wall
        if (tile === 1) {
          const body: CollisionBody = {
            id: generateId('wall'),
            position: {
              x: x * this.data.tileSize,
              y: y * this.data.tileSize,
            },
            velocity: { x: 0, y: 0 },
            collider: {
              type: ColliderType.Box,
              offset: { x: 0, y: 0 },
              width: this.data.tileSize,
              height: this.data.tileSize,
            },
            layer: CollisionLayers.WALL,
            mask: CollisionLayers.ALL,
            isStatic: true,
            isTrigger: false,
          };
          this.collisionBodies.push(body);
        }
      }
    }

    // Add prop colliders
    for (const prop of this.data.props) {
      if (prop.collider) {
        const body: CollisionBody = {
          id: generateId('prop'),
          position: prop.position,
          velocity: { x: 0, y: 0 },
          collider: {
            type: ColliderType.Box,
            offset: { x: 0, y: 0 },
            width: prop.collider.width,
            height: prop.collider.height,
          },
          layer: CollisionLayers.WALL,
          mask: CollisionLayers.ALL,
          isStatic: true,
          isTrigger: false,
        };
        this.collisionBodies.push(body);
      }
    }
  }

  registerCollision(world: CollisionWorld): void {
    for (const body of this.collisionBodies) {
      world.addBody(body);
    }
  }

  getSpawnPoints(team: Team): SpawnPoint[] {
    return this.data.spawnPoints.filter(sp => sp.team === team);
  }

  getSpawnPoint(team: Team, index: number): Vector2 {
    const spawns = this.getSpawnPoints(team);
    const spawn = spawns[index % spawns.length];
    return spawn ? { ...spawn.position } : { x: 0, y: 0 };
  }

  getSpikeSites(): Rectangle[] {
    return this.data.spikeSites;
  }

  isInSpikeSite(position: Vector2): number {
    for (let i = 0; i < this.data.spikeSites.length; i++) {
      const site = this.data.spikeSites[i]!;
      if (
        position.x >= site.x &&
        position.x <= site.x + site.width &&
        position.y >= site.y &&
        position.y <= site.y + site.height
      ) {
        return i;
      }
    }
    return -1;
  }

  getTileAt(worldX: number, worldY: number): number {
    const tileX = Math.floor(worldX / this.data.tileSize);
    const tileY = Math.floor(worldY / this.data.tileSize);

    if (tileY < 0 || tileY >= this.data.tiles.length) return 1;
    const row = this.data.tiles[tileY];
    if (!row || tileX < 0 || tileX >= row.length) return 1;

    return row[tileX] ?? 1;
  }

  isWalkable(worldX: number, worldY: number): boolean {
    return this.getTileAt(worldX, worldY) === 0;
  }

  getWidth(): number {
    return this.data.width;
  }

  getHeight(): number {
    return this.data.height;
  }

  getTileSize(): number {
    return this.data.tileSize;
  }
}

// ============================================================================
// MAP REGISTRY - All available maps
// ============================================================================

export interface MapInfo {
  id: string;
  name: string;
  description: string;
  hasSpikeSites: boolean;
  recommended: string; // Recommended game mode
}

// Registry of all available maps
export const MAP_REGISTRY: MapInfo[] = [
  // Deathmatch maps (no spike sites)
  { id: 'neon_arena', name: 'Neon Arena', description: 'Classic symmetrical arena', hasSpikeSites: false, recommended: 'Deathmatch' },
  { id: 'reactor_core', name: 'Reactor Core', description: 'Circular arena with central cover', hasSpikeSites: false, recommended: 'Deathmatch' },
  { id: 'grid_maze', name: 'Grid Maze', description: 'Complex maze with many corners', hasSpikeSites: false, recommended: 'Deathmatch' },
  { id: 'neon_tower', name: 'Neon Tower', description: 'Vertical tower with open floors', hasSpikeSites: false, recommended: 'Deathmatch' },
  { id: 'crossfire', name: 'Crossfire', description: 'Four-way intersection battle', hasSpikeSites: false, recommended: 'Deathmatch' },
  { id: 'warehouse', name: 'Warehouse', description: 'Industrial storage facility', hasSpikeSites: false, recommended: 'Deathmatch' },
  // Spike maps (have spike sites)
  { id: 'cyber_district', name: 'Cyber District', description: 'Urban streets with spike sites', hasSpikeSites: true, recommended: 'Data Spike' },
  { id: 'helix_labs', name: 'Helix Labs', description: 'Tight corridors and open labs', hasSpikeSites: true, recommended: 'Data Spike' },
  { id: 'sky_bridge', name: 'Sky Bridge', description: 'Three lanes connected by bridges', hasSpikeSites: true, recommended: 'Data Spike' },
  { id: 'data_vault', name: 'Data Vault', description: 'Secure vault with multiple entries', hasSpikeSites: true, recommended: 'Data Spike' },
  { id: 'server_farm', name: 'Server Farm', description: 'High-tech data center', hasSpikeSites: true, recommended: 'Data Spike' },
  { id: 'nexus_hub', name: 'Nexus Hub', description: 'Central network control room', hasSpikeSites: true, recommended: 'Data Spike' },
];

// Get map by ID
export function getMapById(mapId: string): GameMap {
  switch (mapId) {
    case 'neon_arena':
      return createNeonArenaMap();
    case 'cyber_district':
      return createCyberDistrictMap();
    case 'helix_labs':
      return createHelixLabsMap();
    case 'reactor_core':
      return createReactorCoreMap();
    case 'grid_maze':
      return createGridMazeMap();
    case 'sky_bridge':
      return createSkyBridgeMap();
    case 'data_vault':
      return createDataVaultMap();
    case 'neon_tower':
      return createNeonTowerMap();
    case 'crossfire':
      return createCrossfireMap();
    case 'warehouse':
      return createWarehouseMap();
    case 'server_farm':
      return createServerFarmMap();
    case 'nexus_hub':
      return createNexusHubMap();
    default:
      console.warn(`Unknown map ID: ${mapId}, falling back to neon_arena`);
      return createNeonArenaMap();
  }
}

// ============================================================================
// MAP 1: NEON ARENA - Classic symmetrical deathmatch arena (no spike sites)
// ============================================================================

export function createNeonArenaMap(): GameMap {
  const tileSize = 32;
  const width = 40;
  const height = 30;

  const tiles: number[][] = [];
  for (let y = 0; y < height; y++) {
    const row: number[] = [];
    for (let x = 0; x < width; x++) {
      // Border walls
      if (x === 0 || x === width - 1 || y === 0 || y === height - 1) {
        row.push(1);
      }
      // Center pillars (larger)
      else if (x >= 18 && x <= 21 && y >= 13 && y <= 16) {
        row.push(1);
      }
      // Left side cover (staggered)
      else if (
        (x >= 6 && x <= 8 && y >= 8 && y <= 10) ||
        (x >= 10 && x <= 12 && y >= 13 && y <= 16) ||
        (x >= 6 && x <= 8 && y >= 19 && y <= 21)
      ) {
        row.push(1);
      }
      // Right side cover (mirrored)
      else if (
        (x >= 31 && x <= 33 && y >= 8 && y <= 10) ||
        (x >= 27 && x <= 29 && y >= 13 && y <= 16) ||
        (x >= 31 && x <= 33 && y >= 19 && y <= 21)
      ) {
        row.push(1);
      }
      // Mid lane walls
      else if (
        (x >= 15 && x <= 17 && y === 8) ||
        (x >= 22 && x <= 24 && y === 8) ||
        (x >= 15 && x <= 17 && y === 21) ||
        (x >= 22 && x <= 24 && y === 21)
      ) {
        row.push(1);
      }
      else {
        row.push(0);
      }
    }
    tiles.push(row);
  }

  const mapData: MapData = {
    id: 'neon_arena',
    name: 'Neon Arena',
    width: width * tileSize,
    height: height * tileSize,
    tileSize,
    tiles,
    spawnPoints: [
      // Attackers (left side)
      { position: { x: 80, y: 240 }, team: Team.Attackers, index: 0 },
      { position: { x: 80, y: 340 }, team: Team.Attackers, index: 1 },
      { position: { x: 80, y: 440 }, team: Team.Attackers, index: 2 },
      { position: { x: 80, y: 540 }, team: Team.Attackers, index: 3 },
      // Defenders (right side)
      { position: { x: 1200, y: 240 }, team: Team.Defenders, index: 0 },
      { position: { x: 1200, y: 340 }, team: Team.Defenders, index: 1 },
      { position: { x: 1200, y: 440 }, team: Team.Defenders, index: 2 },
      { position: { x: 1200, y: 540 }, team: Team.Defenders, index: 3 },
    ],
    spikeSites: [], // No spike sites - deathmatch map
    reactorPath: [],
    props: [],
  };

  return new GameMap(mapData);
}

// ============================================================================
// MAP 2: CYBER DISTRICT - Urban streets with two spike sites
// ============================================================================

export function createCyberDistrictMap(): GameMap {
  const tileSize = 32;
  const width = 45;
  const height = 32;

  const tiles: number[][] = [];
  for (let y = 0; y < height; y++) {
    const row: number[] = [];
    for (let x = 0; x < width; x++) {
      // Border walls
      if (x === 0 || x === width - 1 || y === 0 || y === height - 1) {
        row.push(1);
      }
      // Left spawn building
      else if (x >= 2 && x <= 6 && y >= 2 && y <= 6) {
        row.push(1);
      }
      else if (x >= 2 && x <= 6 && y >= height - 7 && y <= height - 3) {
        row.push(1);
      }
      // Right spawn building  
      else if (x >= width - 7 && x <= width - 3 && y >= 2 && y <= 6) {
        row.push(1);
      }
      else if (x >= width - 7 && x <= width - 3 && y >= height - 7 && y <= height - 3) {
        row.push(1);
      }
      // Site A area walls (top-right quadrant)
      else if (x >= 28 && x <= 30 && y >= 4 && y <= 8) {
        row.push(1);
      }
      else if (x >= 34 && x <= 36 && y >= 8 && y <= 10) {
        row.push(1);
      }
      // Site B area walls (bottom-right quadrant)
      else if (x >= 28 && x <= 30 && y >= height - 9 && y <= height - 5) {
        row.push(1);
      }
      else if (x >= 34 && x <= 36 && y >= height - 11 && y <= height - 9) {
        row.push(1);
      }
      // Middle corridor walls
      else if (x >= 14 && x <= 16 && y >= 12 && y <= 14) {
        row.push(1);
      }
      else if (x >= 14 && x <= 16 && y >= height - 15 && y <= height - 13) {
        row.push(1);
      }
      // Central building
      else if (x >= 20 && x <= 24 && y >= 13 && y <= 18) {
        row.push(1);
      }
      else {
        row.push(0);
      }
    }
    tiles.push(row);
  }

  const mapData: MapData = {
    id: 'cyber_district',
    name: 'Cyber District',
    width: width * tileSize,
    height: height * tileSize,
    tileSize,
    tiles,
    spawnPoints: [
      // Attackers (left side)
      { position: { x: 80, y: 300 }, team: Team.Attackers, index: 0 },
      { position: { x: 80, y: 400 }, team: Team.Attackers, index: 1 },
      { position: { x: 80, y: 500 }, team: Team.Attackers, index: 2 },
      { position: { x: 80, y: 600 }, team: Team.Attackers, index: 3 },
      // Defenders (right side, near sites)
      { position: { x: width * tileSize - 100, y: 200 }, team: Team.Defenders, index: 0 },
      { position: { x: width * tileSize - 100, y: 300 }, team: Team.Defenders, index: 1 },
      { position: { x: width * tileSize - 100, y: 700 }, team: Team.Defenders, index: 2 },
      { position: { x: width * tileSize - 100, y: 800 }, team: Team.Defenders, index: 3 },
    ],
    spikeSites: [
      { x: 960, y: 128, width: 224, height: 160 },   // Site A (top-right)
      { x: 960, y: height * tileSize - 288, width: 224, height: 160 },  // Site B (bottom-right)
    ],
    reactorPath: [],
    props: [],
  };

  return new GameMap(mapData);
}

