// ============================================================================
// MAP 9: CROSSFIRE - Four-way intersection with open center
// ============================================================================

export function createCrossfireMap(): GameMap {
  const tileSize = 32;
  const width = 40;
  const height = 40;
  const centerX = Math.floor(width / 2);
  const centerY = Math.floor(height / 2);

  const tiles: number[][] = [];
  for (let y = 0; y < height; y++) {
    const row: number[] = [];
    for (let x = 0; x < width; x++) {
      // Border walls
      if (x === 0 || x === width - 1 || y === 0 || y === height - 1) {
        row.push(1);
      }
      // Four corner buildings
      else if (x >= 3 && x <= 10 && y >= 3 && y <= 10) {
        // Top-left building with entry
        if ((x === 10 && y >= 6 && y <= 8) || (y === 10 && x >= 6 && x <= 8)) row.push(0);
        else row.push(1);
      }
      else if (x >= width - 11 && x <= width - 4 && y >= 3 && y <= 10) {
        // Top-right building
        if ((x === width - 11 && y >= 6 && y <= 8) || (y === 10 && x >= width - 9 && x <= width - 7)) row.push(0);
        else row.push(1);
      }
      else if (x >= 3 && x <= 10 && y >= height - 11 && y <= height - 4) {
        // Bottom-left building
        if ((x === 10 && y >= height - 9 && y <= height - 7) || (y === height - 11 && x >= 6 && x <= 8)) row.push(0);
        else row.push(1);
      }
      else if (x >= width - 11 && x <= width - 4 && y >= height - 11 && y <= height - 4) {
        // Bottom-right building
        if ((x === width - 11 && y >= height - 9 && y <= height - 7) || (y === height - 11 && x >= width - 9 && x <= width - 7)) row.push(0);
        else row.push(1);
      }
      // Center cover (small cross)
      else if (
        (x >= centerX - 2 && x <= centerX + 2 && y >= centerY - 1 && y <= centerY + 1) ||
        (x >= centerX - 1 && x <= centerX + 1 && y >= centerY - 2 && y <= centerY + 2)
      ) {
        row.push(1);
      }
      else {
        row.push(0);
      }
    }
    tiles.push(row);
  }

  const mapData: MapData = {
    id: 'crossfire',
    name: 'Crossfire',
    width: width * tileSize,
    height: height * tileSize,
    tileSize,
    tiles,
    spawnPoints: [
      { position: { x: 64, y: centerY * tileSize - 50 }, team: Team.Attackers, index: 0 },
      { position: { x: 64, y: centerY * tileSize + 50 }, team: Team.Attackers, index: 1 },
      { position: { x: 100, y: centerY * tileSize - 100 }, team: Team.Attackers, index: 2 },
      { position: { x: 100, y: centerY * tileSize + 100 }, team: Team.Attackers, index: 3 },
      { position: { x: width * tileSize - 64, y: centerY * tileSize - 50 }, team: Team.Defenders, index: 0 },
      { position: { x: width * tileSize - 64, y: centerY * tileSize + 50 }, team: Team.Defenders, index: 1 },
      { position: { x: width * tileSize - 100, y: centerY * tileSize - 100 }, team: Team.Defenders, index: 2 },
      { position: { x: width * tileSize - 100, y: centerY * tileSize + 100 }, team: Team.Defenders, index: 3 },
    ],
    spikeSites: [],
    reactorPath: [],
    props: [],
  };

  return new GameMap(mapData);
}

