// ============================================================================
// EFFECT SYSTEM - Manages status effects, buffs, debuffs, and ability effects
// ============================================================================

import { EntityId, Vector2 } from '@/shared/types';
import { generateId } from '@/shared/utils';

// ----------------------------------------------------------------------------
// EFFECT TYPES
// ----------------------------------------------------------------------------

export enum EffectType {
  // Damage over time
  Poison = 'poison',
  Burn = 'burn',
  Bleed = 'bleed',
  
  // Healing over time
  Regen = 'regen',
  HealBurst = 'heal_burst',
  
  // Crowd Control
  Stun = 'stun',
  Slow = 'slow',
  Root = 'root',           // Cannot move but can shoot
  Silence = 'silence',     // Cannot use abilities
  Blind = 'blind',         // Reduced vision
  
  // Defensive
  Shield = 'shield',
  DamageReduction = 'damage_reduction',
  Immunity = 'immunity',
  FrontShield = 'front_shield',    // Directional damage reduction (from front only)
  
  // Offensive
  DamageBoost = 'damage_boost',
  FireRateBoost = 'fire_rate_boost',
  CritBoost = 'crit_boost',
  
  // Movement
  SpeedBoost = 'speed_boost',
  SpeedReduction = 'speed_reduction',
  
  // Stealth
  Cloak = 'cloak',
  Revealed = 'revealed',
  
  // Special
  Marked = 'marked',       // Takes bonus damage
  Invulnerable = 'invulnerable',
  Phased = 'phased',       // Can pass through walls
}

export enum EffectCategory {
  Buff = 'buff',
  Debuff = 'debuff',
  Neutral = 'neutral',
}

// How effects stack when multiple are applied
export enum StackBehavior {
  None = 'none',           // Only one instance, ignore new
  Refresh = 'refresh',     // Reset duration on new application
  Stack = 'stack',         // Add stacks up to max
  Replace = 'replace',     // Replace with new (stronger wins)
  Separate = 'separate',   // Each source has separate instance
}

// ----------------------------------------------------------------------------
// EFFECT DEFINITION
// ----------------------------------------------------------------------------

export interface EffectDefinition {
  type: EffectType;
  category: EffectCategory;
  stackBehavior: StackBehavior;
  maxStacks: number;
  priority: number;        // Higher = processed first (for conflicts)
  cleansable: boolean;     // Can be removed by cleanse abilities
  dispellable: boolean;    // Can be removed by dispel/purge
}

